<template>
  <section class="hero2 flex-container">
    <swiper
      class="swiper-layout"
      :modules="[Navigation, Autoplay]"
      slides-per-view="1"
      :space-between="0"
      :autoplay="{ delay: 3000, disableOnInteraction: false, pauseOnMouseEnter: false }"
      :speed="1500"
      navigation
    >
      <swiper-slide 
        v-for="(imageUrl, index) in imageLinks" 
        :key="index" 
        class="swiper-slide"
      >
        <div class="image-wrapper">
          <img :src="getImage(imageUrl)" alt="Hero image" />
        </div>
      </swiper-slide>
    </swiper>
    <div class="txt-wrap" >
      <h1>CHAIKINA ENTERTAINMENT</h1>
      <h2>PERSONALIZED SHOW PRODUCTION GLOBAL TALENT RECRUITMENT</h2>
      <Button text="Create Your Dream Event" buttonStyle="btn-primary"></Button>
    </div>
  </section>
</template>

<script>
import { Swiper, SwiperSlide } from "swiper/vue";
import { Autoplay, Navigation } from "swiper/modules"; // Import the Navigation module
import "swiper/swiper-bundle.css";
import Button from "../Button.vue";
export default {
  components: { Swiper, SwiperSlide,Button },
  setup() {
    return {
      Navigation,
      Autoplay,
    };
  },
  data() {
    return {
      imageLinks: ["1.jpg", "2.jpg", "3.jpg", "4.jpg", "5.jpg", "6.jpg"],
    };
  },
  methods: {
    getImage(imgUrl) {
      return require("@/assets/images/heroImages/" + imgUrl);
    },
  },
};
</script>

<style scoped lang="scss">
.hero2 {
  height: 100vh;
//   width: 100vw;
  position: relative;
  flex-direction: column;
  .txt-wrap {
    position:absolute;
    z-index:1;
    color: $white;
    display:flex;
    flex-direction: column;
    justify-content:center;
    align-items:center;
    text-align: center;
    width:65vw;
    margin-top:10vh;


    h1 {
      font-size:110px;
      font-family: "Libre Baskerville", serif;
    }
    
    h2 {
      font-size: 26px;
      color: $grey;
      padding-top: 2rem;
      width:fit-content;
    }

    Button {
      margin-top:96px;
    }
  }
  .swiper-layout {
    width: 100%;
    height: 100vh;

    .swiper-button-next,
    .swiper-button-prev {
      color: $yellow;
    }

    .swiper-pagination-bullet-active {
      background: white;
    }

    .swiper-slide {
        position: relative;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        
        .image-wrapper {
            width: 100%;
            height: 100%;
            overflow: hidden;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover; /* Ensures the image fills the container while maintaining aspect ratio */
          object-position:top;
        }
        
        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.4); // Semi-transparent black overlay
            z-index: 1; // Places the overlay above the image
        }
      }
    }
  }
}

@media(max-width:1200px) {
  .hero2 {
    .txt-wrap {
      h1 {font-size:70px;}
    }
  }
}

@media(max-width:800px) {
  .hero2 {
    .txt-wrap {
      h1 {font-size:30px;}
    }
  }
}
</style>
